import styled from 'styled-components';

import bgImage from '../../img/bg.png';

export const StyledTetrisWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  background: url(${bgImage}) #000;
  background-size: cover;
  overflow: hidden;
`;

export const StyledTetris = styled.div`
  display: flex;
  align-items: flex-start;
  padding: 40px;
  margin: 0 auto;
  max-width: 900px;

  aside {
    width: 100%;
    max-width: 200px;
    display: block;
    padding: 0 20px;
  }

  @media screen and (max-width: 800px) {
    display: block;
    max-width: unset;

    aside {
      position: absolute;
      bottom: 5vw;
      display: flex;
      align-items: center;
      width: 80vw;
      height: 10vh;
      max-width: unset;
    }
  }
`;
